@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  section {
    @apply py-[75px];
  }
}

@layer components {
  .container {
    @apply max-w-full w-[1440px] px-5 mx-auto;
  }

  .btn {
    @apply bg-primaryColor py-[15px] px-[35px] rounded-[50px] text-white font-[600] mt-[38px];
  }

  .heading {
    @apply text-[44px] leading-[80px] font-[700] text-headingColor;
  }

  .text__para {
    @apply text-[18px] leading-[30px] font-[400] text-textColor mt-[18px];
  }

  /* Initial Header - Blue Background */
  .header {
    @apply bg-[#0047ab] text-white w-full h-[100px] leading-[100px] transition-all duration-300 ease-in-out;
  }

  /* Scrolled Header - White Background and Shadow */
  .scrolled {
    @apply bg-white shadow-md;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }

  /* Initial Navigation Link Style - White */
  .nav__link-initial {
    @apply text-white font-medium hover:text-gray-200 transition-colors duration-300;
  }

  /* Scrolled Navigation Link Style - Black */
  .nav__link-scrolled {
    @apply text-black font-medium hover:text-gray-600 transition-colors duration-300;
  }

  .hero__section {
    @apply bg-[url('./assets/images/homebg.jpg')] bg-no-repeat bg-center bg-cover;
  }

  .form__label {
    @apply text-textColor font-semibold text-[16px] leading-7 mb-2;
  }

  .form__input {
    @apply w-full px-4 py-3 border border-solid border-[#0066ff61] focus:outline-none focus:border-primaryColor text-[16px] leading-7 text-headingColor placeholder:text-textColor cursor-pointer rounded-full;
  }

  .form__input-1 {
    @apply w-full px-4 py-3 border border-solid border-[#0066ff61] focus:outline-none focus:border-primaryColor text-[16px] leading-7 text-headingColor placeholder:text-textColor cursor-pointer rounded-md;
  }

}

@media only screen and (max-width: 1024px) {
  .container {
    @apply w-full;
  }

  section {
    @apply py-[35px];
  }
}

@media only screen and (max-width: 768px) {
  .heading {
    @apply text-[26px] leading-[36px];
  }

  .text__para {
    @apply text-[16px] leading-7 mt-3;
  }
}